import React, { Component } from 'react'
import { Grid, Image, Container, Icon, Form, Input, TextArea, Button, Select } from 'semantic-ui-react'
export class Contact extends Component {
    render() {
        return (
            <section>
                <div className="contact">
                    <Container>
                        <h1 style={{ textAlign: 'center', marginBottom: '15px' }}>Get in touch</h1>
                        <Grid container style={{ marginTop: '35px' }}>
                            <Grid.Column mobile={16} tablet={8} computer={8}>
                                <Grid style={{ marginBottom: '20px' }}>
                                    <Grid.Column width={3}>
                                        <Icon size="huge" name="map marker alternate" />
                                    </Grid.Column>
                                    <Grid.Column width={13}>
                                        <h3>Address</h3>
                                        <p>407-389 Garfield Ave Mineola, NY 11501, New york</p>
                                    </Grid.Column>
                                </Grid>
                                <Grid style={{ marginBottom: '20px' }}>
                                    <Grid.Column width={3}>
                                        <Icon size="huge" name="mobile alternate" />
                                    </Grid.Column>
                                    <Grid.Column width={13}>
                                        <h3>Phone</h3>
                                        <p>( 98 ) 234 596 | ( 98 ) 345 687</p>
                                    </Grid.Column>
                                </Grid>
                                <Grid style={{ marginBottom: '20px' }}>
                                    <Grid.Column width={3}>
                                        <Icon size="huge" name="envelope outline" />
                                    </Grid.Column>
                                    <Grid.Column width={13}>
                                        <h3>Email</h3>
                                        <p>Beauty.hairsalon@gamail.com</p>
                                    </Grid.Column>
                                </Grid>
                            </Grid.Column>
                            <Grid.Column width={8}>
                                <Form>
                                    <Form.Group widths='equal'>
                                        <Form.Field
                                            id='form-input-control-first-name'
                                            control={Input}
                                            placeholder='Name*'
                                        />
                                       
                                    </Form.Group>
                                    <Form.Group widths='equal'>
                                    <Form.Field
                                            id='form-input-control-error-email'
                                            control={Input}
                                            placeholder='Email*'

                                        />
                                        
                                    </Form.Group>
                                    <Form.Group widths='equal'>
                                    <Form.Field
                                            id='form-input-control-subject'
                                            control={Input}
                                            placeholder='Subject*'

                                        />
                                    </Form.Group>
                                    <Form.Field
                                        id='form-textarea-control-message'
                                        control={TextArea}
                                        placeholder='Message*'
                                    />

                                    <Form.Field
                                        id='form-button-control-public'
                                        control={Button}
                                        content='Send Message'
                                        color='black'
                                    />
                                </Form>
                            </Grid.Column>
                        </Grid>
                    </Container>
                </div>
            </section>
        )
    }
}

export default Contact
