import React, { Component } from 'react'
import { Grid, Container, Header, Icon } from 'semantic-ui-react'

export class Skills extends Component {
    render() {
        return (
            <section style={{ backgroundColor: '#f7f7f7', borderTop: '1px solid #eee', borderBottom: '1px solid #eee' }}>
                <div className="skills" style={{ textAlign: 'center', }}>
                    <Container>
                        <h1>My Skills</h1>
                        <Header as='h2'>Here are some of my technical skills</Header>
                        <Grid container style={{ marginTop: '35px' }}>
                            <Grid.Column mobile={16} tablet={8} computer={5} style={{ textAlign: 'center', marginBottom: '15px' }}>
                                <Icon name="vuejs" size="huge" />
                                <h3>Vue js</h3>
                                <p>The case has been an interesting one," remarked Holmes when our visitors had left us</p>
                            </Grid.Column>
                            <Grid.Column mobile={16} tablet={8} computer={5} style={{ textAlign: 'center', marginBottom: '15px' }}>
                                <Icon name="react" size="huge" />
                                <h3>React js</h3>
                                <p>The case has been an interesting one," remarked Holmes when our visitors had left us</p>
                            </Grid.Column>
                            <Grid.Column mobile={16} tablet={8} computer={5} style={{ textAlign: 'center', marginBottom: '15px' }}>
                                <Icon name="html5" size="huge" />
                                <h3>HTML 5</h3>
                                <p>The case has been an interesting one," remarked Holmes when our visitors had left us</p>
                            </Grid.Column>
                            <Grid.Column mobile={16} tablet={8} computer={5} style={{ textAlign: 'center', marginBottom: '15px' }}>
                                <Icon name="css3" size="huge" />
                                <h3>CSS3</h3>
                                <p>The case has been an interesting one," remarked Holmes when our visitors had left us</p>
                            </Grid.Column>
                            <Grid.Column mobile={16} tablet={8} computer={5} style={{ textAlign: 'center', marginBottom: '15px' }}>
                                <Icon name="js" size="huge" />
                                <h3>Javascript</h3>
                                <p>The case has been an interesting one," remarked Holmes when our visitors had left us</p>
                            </Grid.Column>

                            <Grid.Column mobile={16} tablet={8} computer={5} style={{ textAlign: 'center', marginBottom: '15px' }}>
                                <Icon name="mobile alternate" size="huge" />
                                <h3>Responsive design</h3>
                                <p>The case has been an interesting one," remarked Holmes when our visitors had left us</p>
                            </Grid.Column>
                        </Grid>
                    </Container>
                </div>
            </section>
        )
    }
}

export default Skills
