import React, { Component } from 'react'
import bannerImage from '../assets/c.jpg'
import { Icon } from 'semantic-ui-react'


export class Banner extends Component {
    render() {
        return (
            <div className="banner" style={{
                backgroundPosition: 'bottom',
                backgroundSize: 'cover',
                backgroundRepeat: 'no-repeat', backgroundImage: `url(${bannerImage})`,
            }}>

                <h1>Hey there, I am a web developer</h1>
                <p>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Tenetur deserunt nostrum iure qui temporibus iste maiores placeat? Voluptatibus, obcaecati pariatur?
                </p>
                <div>
                    <a href="#">
                        <Icon name='facebook f' />
                    </a>
                    <a href="#">
                        <Icon name='instagram' />
                    </a>
                    <a href="#">
                        <Icon name='twitter' />
                    </a>
                    <a href="#">
                        <Icon name='linkedin' />
                    </a>

                </div>
            </div>
        )
    }
}

export default Banner
